.profile-buttons_container {
	height: 4em;
	cursor: pointer;
}


.profile-buttons_container p {
	font-size: 1.5em;
	height: 100%;
    margin: -2px;
}

.profile-button::after {
	content: '';
	height: 5px;
	display: block;
	background-color: blueviolet;
	margin: -0.5px;
}

.active::after {
	height: 3px;
}
.active p {
	margin: -1px;
}

.profile-button:hover::after  {
	height: 3px;
}

.profile-button:hover p  {
	margin: -1px;
}